.slider-container {
    --progress-bar-height: 4px;
}

.slider-container {
    margin: auto;
    position: relative;
    width: 75%;
}

/* Background */
.slider-container::before {
    content: '';
    background-color: black;
    width: 99%;
    height: calc(var(--progress-bar-height) - 1px);
    display: block;
    position: absolute;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    pointer-events: none;
}

/* Custom Progress Bar */
.progress-bar-cover {
    background-color: rgb(218, 55, 145);
    width: 100%;
    height: var(--progress-bar-height);
    display: block;
    position: absolute;
    border-radius: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 1;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
}

/*  Hide Original */
.range {
    -webkit-appearance: none;
    background-color: rgba(240, 9, 9, 0.397);
    height: 10px;
    width: 100%;
    cursor: pointer;
    opacity: 0;
    margin: auto;
}
.slider-container {
    --thumb-width: 20px;
    --thumb-height: 20px;
}

.range::-webkit-slider-thumb {
    width: var(--thumb-width);
    height: var(--thumb-height);

    background: #350f2d;
    border: 1px solid #000000;
    border-radius: 50%;
    cursor: pointer;
    -webkit-appearance: none;
}

.thumb {
    width: var(--thumb-width);
    height: var(--thumb-height);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.753);
    z-index: 3;
    background: black;
    position: absolute;
    border-radius: 50%;
    top: 50%;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
    pointer-events: none;
    /* Remove pointer events on thumb so user can click on the actual thumb beaneath it!  */
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /*  Prevent Accidentally highlighting the number while sliding the cursor  */
}
